.symbol-group {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center
}
.symbol-group .symbol {
	position: relative;
	z-index: 0;
	margin-left: -10px;
	border: 2px solid #fff;
	-webkit-transition: all .3s ease;
	transition: all .3s ease
}
.symbol-group .symbol:first-child {
	margin-left: 0
}
.symbol-group .symbol:hover {
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
	z-index: 1
}
.symbol-group.symbol-hover .symbol {
	cursor: pointer
}
.symbol-list {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center
}
.symbol-list .symbol {
	margin-right: .5rem
}
.symbol-list .symbol:last-child {
	margin-right: 0
}
.pulse {
	position: relative
}
.pulse .pulse-ring {
	display: block;
	border-radius: 40px;
	height: 40px;
